@import '../../assets/sass/colors';
@import '../../assets/sass/share';
@import '../../components/header/header';
@import '../../components/skills/skills';
@import '../../components/footer/footer.scss';
@import '../../components/projects/projects.scss';

body{
    background-color: $midColor;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: $lightColor;
    padding: 0;
    margin: 0;
}
.spinner{
    position: absolute !important;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #eee;
    z-index: +1;
    transition: all ease 1.2s;
    &::before{
        content: 'Loading';
        display: block;
        position: absolute;
        color: $midColor;
        top: 50%;
        left: 50%;
        z-index: +2;
        animation: animatLodingWord .4s ease infinite;
    }
    &::after{
        content: '';
        display: block;
        position: absolute;
        width: 10px;
        height: 5px;
        background-color: $midColor;
        top: 53%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        z-index: +2;
        animation: animatLodingBorder .6s ease infinite;

    }
}

@keyframes animatLodingWord{
    0%{
        transform: scale(1) translateX(-50%) translateY(-50%);
        -o-transform: scale(1) translateX(-50%) translateY(-50%);
        -webkit-transform: scale(1) translateX(-50%) translateY(-50%);
        -moz-transform: scale(1) translateX(-50%) translateY(-50%);
    }
    50%{
        transform: scale(1.2) translateX(-50%) translateY(-50%);
        -o-transform: scale(1.2) translateX(-50%) translateY(-50%);
        -webkit-transform: scale(1.2) translateX(-50%) translateY(-50%);
        -moz-transform: scale(1.2) translateX(-50%) translateY(-50%);
    }
    100%{
        transform: scale(1) translateX(-50%) translateY(-50%);
        -o-transform: scale(1) translateX(-50%) translateY(-50%);
        -webkit-transform: scale(1) translateX(-50%) translateY(-50%);
        -moz-transform: scale(1) translateX(-50%) translateY(-50%);
    }
}
@keyframes animatLodingBorder{
    0%{
        transform:translateX(200%);
    }
    50%{
        transform:translateX(-300%) ;
    }
    100%{
        transform:translateX(200%);
    }
}