@import '../../assets/sass/colors';

.skills{
    ul{
        width: 50%;
        float: left;
        list-style: square;
        line-height: 25px;
    }
}
@media(max-width:578px){
.skills{
    ul{
        width: 100%;
        margin: 0 5px;
        &:last-of-type{
            padding-bottom: 20px;
        }
    }
}
}